const PersonForm = (props) => {
    return (
        <form onSubmit={props.addNewName}>
        <h2>add new </h2>
        <div>
          name :  
          <input 
              value={props.newName}
              onChange={props.handleNewName}
          />
        </div>
        <div>
          phone:
          <input
              value={props.newPhone}
              onChange={props.handleNewPhone}
          />
        </div>
        <div>
          <button type="submit">add</button>
        </div>
      </form>
    )
}

export default PersonForm