import React, { useState, useEffect } from 'react'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import Persons from './components/Persons'
import Notify from './components/Notify'
import personServices from './services/persons'

import './index.css'

const App = () => {
  const [ persons, setPersons ] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newPhone, setNewPhone ] = useState('')
  const [ nameFilter, setNameFilter] = useState('')
  const [ errorMessage, setErrorMessage ] = useState(null)
  const [ isError, setIsError] = useState(false)

  // handle filter input field
  const handleNewFilter = (e) => 
          setNameFilter(e.target.value)
  
  // handle input from name field
  const handleNewName = (e) => 
          setNewName(e.target.value)

  // handle input from phone input field  
  const handleNewPhone = (e) => 
          setNewPhone(e.target.value);


  // fetch persons data from db.js server
  useEffect(() => {
    //console.log("effect");
    personServices
      .getAll()
      .then(res => {
        //console.log("fetched");
        setPersons(res)
      })
  }, [])

  // filter people by entered filter word 
  const filterPeople = 
        persons.filter( person => 
            person
              .name
              .toLowerCase()
              .includes(nameFilter.toLowerCase())
              )

  // add new person in phone book
  const addNewName = (e) => {
    e.preventDefault();

    const newPersonInfo = {
      name: newName,
      number: newPhone
    }
    console.log(persons)
    console.log(persons.some(e => e.name === newName))

    // handle update phoneBook based on condition
    if (persons.some(e => e.name === newName)){
      // check if phone already exist in phone book
      const update = window.confirm(`person '${newName}' already exists in the phonebook, 
                                          \nDo you really want to update its number?`)

      // if user clicks "OK" button, peeson's phone number will be updated
      if (update){
        udpatePerson(newPersonInfo);
      } else {
        console.log("request on update was rejected");
      }

    } else {
      // if not exist entered phone add it
       
      personServices
        .create(newPersonInfo)
        .then(res => {
          //console.log("adding name...", res.name);
          setPersons(persons.concat(res));
          setNewName('') // clear name field
          setNewPhone('') // clear phoen field
          // before notification make sure to set error indicator to false
          setIsError(false);
          // make notification
          makeNotification(`added '${res.name}'`);
        })
        .catch (error => {
          console.log("here mast be this test")        
          // before notification make sure to set error indicator to 'true'
          setIsError(true);
          // make notification
          makeNotification(error.response.data.error)
        } )
    }
  }

  // this function is called if user will confirm update allert 
  // it will udpate phone number of the person
  const udpatePerson = (newPersonInfo) => {
        // find person to update
        const personToUpdate = persons.find(person => person.name === newPersonInfo.name);

        // update
        personServices
          .update(personToUpdate.id, newPersonInfo)
          .then(returnedPerson => {
            setPersons(persons.map(person =>
                    person.name !== newPersonInfo.name
                    ? person 
                    : returnedPerson
           ));
          // before notification make sure to set error indicator to false
          setIsError(false);
          // make notification
           makeNotification(`updated '${returnedPerson.name}'` )
          })
          .catch(error => {            
            // before notification make sure to set error indicator to 'true'
            setIsError(true);
            
            // make notification
            if (error.response.data.error){
              makeNotification(error.response.data.error);
            } else {
              makeNotification(`can't update person '${personToUpdate.name}',it is deleted`)    
              // filter persons on the display to remove deleted person from the display
              setPersons(persons.filter(person => person.id !== personToUpdate.id))
            }
          })
  }

  // delete person from the list
  const deletePerson = (id) => {
    const deletedPerson = persons.find(person => person.id === id)
    personServices
      .deleteOne(id)
      .then(res =>{
          // after deletion this will remove person from display   
          setPersons(persons.filter(person => person.id !== id))
          // before notification make sure to set error indicator to false
          setIsError(false);
          // make notification
          makeNotification(`deleted '${deletedPerson.name}'`)
      })
      .catch(error => {
        // filter persons on the display to remove deleted person from the display
        setPersons(persons.filter(person => person.id !== id))
         // before notification make sure to set error indicator to 'true'
         setIsError(true);
         // make notification
        makeNotification(`person '${deletedPerson.name}' is already deleted`)
      })
    
  }

  // create notification about succesful operation
  const makeNotification = (message) => {
    setErrorMessage(
      `${message}`
    )
    // after some time message disappears
    setTimeout(() => {
      setErrorMessage(null)
    }, 3000
    )
  }
  return (
    <>
      <h1>Phonebook</h1>
      <Notify 
          message={errorMessage}
          isError={isError}
          /> 
      <Filter 
          nameFilter={nameFilter}
          handleNewFilter={handleNewFilter} 
          />
      <PersonForm 
          addNewName={addNewName}
          newName={newName}
          handleNewName={handleNewName}
          newPhone={newPhone}
          handleNewPhone={handleNewPhone}
          />
      {/** iterate through persons list and display them */}
      <h2>List</h2>
      <div>
      {
        filterPeople.map(person => {
          return (
            <Persons 
                key={person.number}
                person={person}
                deletePerson={() => deletePerson(person.id)}
                /> 
          )
        })
      }
      </div>
    </>
  )
}

export default App